body {
  font-family: 'ProximaNova-Regular', sans-serif;
  font-size: 14px;
  min-width: 320px;
  max-width: 2560px;
  color: #000;
  position: relative;
  margin: 0 auto;
  padding: 0;
  background: #fff;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  line-height: 1;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

button{
  border: none;
  outline: none;
}

a {
  text-decoration: none;
  color: #333;
}

p {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

input,textarea{
  border:none;
  outline: none;
}

input::-webkit-input-placeholder {
  color: #7c7c7c;
}
input::-moz-placeholder {
  color: #7c7c7c;
}
input:-moz-placeholder {
  color: #7c7c7c;
}
input:-ms-input-placeholder {
  color: #7c7c7c;
}

textarea::-webkit-input-placeholder {
  color: #7c7c7c;
}
textarea::-moz-placeholder {
  color: #7c7c7c;
}
textarea:-moz-placeholder {
  color: #7c7c7c;
}
textarea:-ms-input-placeholder {
  color: #7c7c7c;
}

.container{
  width: 100%;
  max-width: 1170px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

.title{
  font-size: 36px;
  display: block;
  text-align: center;
  letter-spacing: .2px;
}

.top-line{
  background-color: rgb(35, 186, 193);
  width: 31px;
  height: 2px;
  margin: 0 auto;
  margin-top: 22px;
}





@font-face {
  font-family: "ProximaNova-Regular";
  src: url("../fonts/ProximaNova-Regular.eot");
  src: url("../fonts/ProximaNova-Regular.eot?#iefix") format("embedded-opentype"),
  url("../fonts/ProximaNova-Regular.woff") format("woff"),
  url("../fonts/ProximaNova-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}


