body {
  font-family: 'ProximaNova-Regular', sans-serif;
  font-size: 14px;
  min-width: 320px;
  max-width: 2560px;
  color: #000;
  position: relative;
  margin: 0 auto;
  padding: 0;
  background: #fff;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  line-height: 1;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

button {
  border: none;
  outline: none;
}

a {
  text-decoration: none;
  color: #333;
}

p {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

input, textarea {
  border: none;
  outline: none;
}

input::-webkit-input-placeholder {
  color: #7c7c7c;
}

input::-moz-placeholder {
  color: #7c7c7c;
}

input:-moz-placeholder {
  color: #7c7c7c;
}

input:-ms-input-placeholder {
  color: #7c7c7c;
}

textarea::-webkit-input-placeholder {
  color: #7c7c7c;
}

textarea::-moz-placeholder {
  color: #7c7c7c;
}

textarea:-moz-placeholder {
  color: #7c7c7c;
}

textarea:-ms-input-placeholder {
  color: #7c7c7c;
}

.container {
  width: 100%;
  max-width: 1170px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

.title {
  font-size: 36px;
  display: block;
  text-align: center;
  letter-spacing: .2px;
}

.top-line {
  background-color: #23bac1;
  width: 31px;
  height: 2px;
  margin: 0 auto;
  margin-top: 22px;
}

@font-face {
  font-family: "ProximaNova-Regular";
  src: url("../fonts/ProximaNova-Regular.eot");
  src: url("../fonts/ProximaNova-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/ProximaNova-Regular.woff") format("woff"), url("../fonts/ProximaNova-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "ProximaNova-Bold";
  src: url("../fonts/ProximaNova-Bold.eot");
  src: url("../fonts/ProximaNova-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/ProximaNova-Bold.woff") format("woff"), url("../fonts/ProximaNova-Bold.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "ProximaNova-Light";
  src: url("../fonts/ProximaNova-Light.eot");
  src: url("../fonts/ProximaNova-Light.eot?#iefix") format("embedded-opentype"), url("../fonts/ProximaNova-Light.woff") format("woff"), url("../fonts/ProximaNova-Light.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "MyriadProRegular";
  src: url("../fonts/MyriadProRegular.eot");
  src: url("../fonts/MyriadProRegular.eot?#iefix") format("embedded-opentype"), url("../fonts/MyriadProRegular.woff") format("woff"), url("../fonts/MyriadProRegular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

.header {
  width: 100%;
  height: auto;
  position: relative;
  background-image: url(../images/bg-header.png);
  -webkit-background-size: cover;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding-bottom: 90px;
  background-attachment: fixed;
}

.top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0;
}

.logo {
  position: relative;
  transform: translateX(0%);
  transition: .3s;
}

.top-contacts {
  display: flex;
  align-items: center;
  padding: 0 50px;
}

@media screen and (max-width: 1023px) {
  .top-contacts {
    display: none;
  }
}

.top-contacts .city {
  font-size: 13px;
  color: #ffffff;
  position: relative;
  line-height: 1.3;
}

.top-contacts .city:before {
  content: '';
  background-image: url(../images/city.png);
  position: absolute;
  width: 15px;
  height: 20px;
  background-repeat: no-repeat;
  margin-left: -35px;
  top: 50%;
  margin-top: -10px;
}

.top-contacts .mail {
  font-size: 13px;
  color: #ffffff;
  margin-left: 250px;
  position: relative;
}

@media screen and (max-width: 1024px) {
  .top-contacts .mail {
    margin-left: 70px;
  }
}

.top-contacts .mail:before {
  content: '';
  background-image: url(../images/mail.png);
  position: absolute;
  width: 15px;
  height: 10px;
  background-repeat: no-repeat;
  margin-left: -35px;
  top: 50%;
  margin-top: -5px;
}

@media screen and (max-width: 1023px) {
  .top-right {
    display: none;
  }
}

.top-right .phone {
  color: #ffffff;
  font-size: 18px;
  margin-bottom: 10px;
  text-align: right;
  display: block;
}

.top-right .phone:hover {
  color: #00d1f9;
}

.top-right .header-btn-modal {
  color: #00d1f9;
  font-size: 13px;
  display: block;
  text-decoration: underline;
  font-family: "ProximaNova-Light";
  cursor: pointer;
}

.top-right .header-btn-modal:hover {
  text-decoration: none;
}

.menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  border-bottom: 1px solid #414141;
  border-top: 1px solid #414141;
}

@media screen and (max-width: 1024px) {
  .menu {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #000000;
    height: 100vh;
    z-index: 101;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 120px 15px;
    transform: translateX(-100%);
    transition: .3s;
  }
}

.menu li {
  cursor: pointer;
}

.menu li a {
  color: #ffffff;
  text-transform: uppercase;
  font-size: 13px;
  cursor: pointer;
}

.menu li a:hover {
  color: #00d1f9;
}

@media screen and (max-width: 1024px) {
  .menu li a {
    font-size: 16px;
    margin-bottom: 15px;
    display: block;
  }
}

.logo-menu {
  display: none;
}

@media screen and (max-width: 1024px) {
  .logo-menu {
    display: block;
    margin-top: -90px;
    margin-bottom: 20px;
  }
}

.menu-open {
  transform: translateX(0%);
}

.nav-top {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100000;
  background-color: rgba(0, 0, 0, 0.9);
  border-bottom: 1px solid #414141;
}

.nav-top .menu {
  width: 100%;
  max-width: 1140px;
  margin: 0 auto;
  border-bottom: 1px solid transparent;
  border-top: 1px solid transparent;
}

.menu-active {
  color: #00d1f9 !important;
}

.hamburger {
  flex-direction: column;
  min-width: 40px;
  min-height: 40px;
  border: 1px solid #fff;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  position: relative;
  z-index: 1000;
  display: none;
}

@media screen and (max-width: 1024px) {
  .hamburger {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.hamburger span {
  width: 22px;
  height: 2px;
  background-color: #ffffff;
  display: block;
  margin-bottom: 3px;
}

.hamburger span:last-child {
  margin-bottom: 0;
}

.hamburger-close {
  padding-top: 3px;
}

.hamburger-close .first {
  transform: rotate(-45deg);
  margin-bottom: 0;
}

.hamburger-close .second {
  transform: rotate(45deg);
  margin-top: -2px;
}

.hamburger-close .third {
  display: none !important;
}

.promo {
  padding-top: 85px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media screen and (max-width: 1023px) {
  .promo {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding-top: 30px;
  }
}

.promo-title {
  color: #ffffff;
  font-size: 36px;
  display: block;
  margin-bottom: 50px;
  line-height: 1.2;
}

@media screen and (max-width: 767px) {
  .promo-title {
    font-size: 19px;
    margin-bottom: 30px;
  }
}

@media screen and (max-width: 374px) {
  .promo-title {
    font-size: 16px;
  }
}

@media screen and (max-width: 1023px) {
  .promo-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    text-align: center;
  }
}

.advantages-title {
  font-size: 20px;
  color: #ffffff;
  display: block;
  margin-bottom: 60px;
}

@media screen and (max-width: 1023px) {
  .advantages-title {
    margin-bottom: 30px;
  }
}

@media screen and (max-width: 767px) {
  .advantages-title {
    font-size: 16px;
  }
}

.advantages-wrap {
  display: flex;
}

@media screen and (max-width: 1023px) {
  .advantages-wrap {
    margin-bottom: 40px;
  }
}

.advantages-block {
  margin-right: 120px;
}

.advantages-block:last-child {
  margin-right: 0;
}

@media screen and (max-width: 767px) {
  .advantages-block {
    margin-right: 30px;
  }
}

.advantages-item {
  display: flex;
  align-items: center;
  margin-bottom: 60px;
}

@media screen and (max-width: 1023px) {
  .advantages-item {
    margin-bottom: 20px;
