}

.advantages-item__img {
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.advantages-item__text {
  font-family: "ProximaNova-Light";
  color: #ffffff;
  font-size: 15px;
  display: block;
  line-height: 1.3;
  margin-left: 40px;
}

@media screen and (max-width: 575px) {
  .advantages-item__text br {
    display: none;
  }
}

@media screen and (max-width: 575px) {
  .advantages-item__text {
    margin-left: 0;
    margin-top: 15px;
    font-size: 13px;
  }
}

.block-form {
  background-color: #ffffff;
  width: 100%;
  max-width: 360px;
  padding: 37px 50px;
}

@media screen and (max-width: 1023px) {
  .block-form {
    max-width: 1023px;
  }
}

@media screen and (max-width: 767px) {
  .block-form {
    padding: 25px 15px;
  }
}

.block-form__title {
  display: block;
  text-align: center;
  font-size: 18px;
  color: #000000;
  margin-bottom: 30px;
  line-height: 1.2;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form input {
  width: 100%;
  height: 48px;
  border: 1px solid #e8e8e8;
  margin-bottom: 10px;
  font-size: 13px;
  text-align: center;
  font-family: "ProximaNova-Light";
  padding: 0 10px;
  color: #7c7c7c;
}

.form textarea {
  width: 100%;
  min-height: 85px;
  border: 1px solid #e8e8e8;
  font-size: 13px;
  text-align: center;
  font-family: "ProximaNova-Light";
  padding: 20px;
  color: #7c7c7c;
}

.form .submit {
  width: 200px;
  height: 55px;
  background-color: #23bac1;
  font-size: 13px;
  color: #ffffff;
  text-transform: uppercase;
  font-family: "ProximaNova-Bold";
  margin-top: 20px;
  box-shadow: 0px 6px 18px 0px rgba(28, 167, 173, 0.32);
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: .3s;
  z-index: 2;
}

.form .submit:hover {
  background-color: #1f969c;
}

.block-form__footer {
  font-size: 11px;
  color: #7c7c7c;
  text-align: center;
  display: block;
  margin-top: 20px;
  font-family: "ProximaNova-Light";
  cursor: pointer;
}

.block-form__footer .link {
  text-decoration: underline;
  color: #7c7c7c;
}

.block-form__footer .link:hover {
  text-decoration: none;
}

.product {
  width: 100%;
