.instruments {
  background-color: #f9f9f9;
}

.product-wrap {
  width: 100%;
  margin: 0 auto;
  margin-top: 100px;
}

@media screen and (max-width: 767px) {
  .product-wrap {
    margin-top: 50px;
  }
}

.product-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 0 auto;
  max-width: 270px;
}

.product-item__img {
  width: 269px;
  height: 270px;
  position: relative;
  overflow: hidden !important;
  top: 0;
}

.product-item__img .owl-nav {
  display: none !important;
}

.product-item__name {
  display: block;
  font-family: "ProximaNova-Light";
  color: #000000;
  font-size: 16px;
  text-align: center;
  line-height: 1.2;
}

.product-item__price {
  display: block;
  font-size: 18px;
  color: #000000;
  font-family: "ProximaNova-Bold";
  margin: 20px 0 25px 0;
}

.product-item__btn {
  font-size: 13px;
  color: #23bac1;
  text-transform: uppercase;
  font-family: "ProximaNova-Bold";
  display: block;
  width: 198px;
  height: 53px;
  border: 1px solid #23bac1;
  cursor: pointer;
  background-color: transparent;
  position: relative;
  padding-left: 35px;
  transition: .3s;
  letter-spacing: .5px;
}

.product-item__btn:hover {
  background-color: #23bac1;
  color: #ffffff;
}

.product-item__btn:hover:before {
  background-image: url(../images/buy1.png);
}

.product-item__btn:before {
  content: '';
  background-image: url(../images/buy.png);
  background-repeat: no-repeat;
  width: 17px;
  height: 16px;
  position: absolute;
  margin-left: -37px;
  top: 50%;
  margin-top: -8px;
  transition: .3s;
}

.owl-nav {
  display: block !important;
}

.owl-prev {
  background-color: transparent !important;
  border-top: 3px solid #cccccc !important;
  border-right: 3px solid #cccccc !important;
  width: 22px;
  height: 22px;
  position: absolute;
  left: -50px;
  top: 50%;
  transform: rotate(-135deg);
}

@media screen and (max-width: 1199px) {
  .owl-prev {
    left: 0;
  }
}

.owl-prev:hover {
  border-top: 3px solid #23bac1 !important;
  border-right: 3px solid #23bac1 !important;
}

.owl-next {
  background-color: transparent !important;
  border-top: 3px solid #cccccc !important;
  border-right: 3px solid #cccccc !important;
  width: 22px;
  height: 22px;
  position: absolute;
  right: -50px;
  top: 50%;
  transform: rotate(45deg);
}

@media screen and (max-width: 1199px) {
  .owl-next {
    right: 0;
  }
}

.owl-next:hover {
  border-top: 3px solid #23bac1 !important;
  border-right: 3px solid #23bac1 !important;
}

.owl-dots {
  display: flex;
  justify-content: center;
  margin-top: 40px;
}

.owl-dots .owl-dot {
  margin-top: -50px;
}

.owl-dots .owl-dot span {
  width: 8px;
  height: 8px;
  position: relative;
  margin: 0 auto !important;
  background-color: #e8e8e8 !important;
  margin-right: 4px !important;
  cursor: pointer;
  display: block !important;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  border-radius: 100%;
}

.owl-dots .active span {
  display: inline-block !important;
  background-color: #23bac1 !important;
  margin-top: 3px !important;
}

