.modal {
  width: 100%;
  max-width: 360px;
  height: auto;
  background-color: transparent;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 10002;
  display: none;
}

.modal .block-form {
  padding-top: 40px;
}

.modal-close {
  position: absolute;
  right: 10px;
  top: 10px;
  opacity: 1;
  z-index: 101;
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.modal-close__first {
  position: absolute;
  background-color: #000;
  width: 20px;
  height: 2px;
  top: 11px;
  left: 3px;
  transform: rotate(45deg);
}

