// Import
@import '../_styles/normalize.scss';



//header
.header {
  width: 100%;
  height: auto;
  position: relative;
  background-image: url(../images/bg-header.png);
  -webkit-background-size: cover;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding-bottom: 90px;
  background-attachment: fixed;
}

.top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0;
}

.logo {
  position: relative;
  transform: translateX(0%);
  transition: .3s;
}



.top-contacts {
  display: flex;
  align-items: center;
  padding: 0 50px;
  @media screen and (max-width: 1023px) {
    display: none;
  }

  .city {
    font-size: 13px;
    color: #ffffff;
    position: relative;
    line-height: 1.3;
    &:before {
      content: '';
      background-image: url(../images/city.png);
      position: absolute;
      width: 15px;
      height: 20px;
      background-repeat: no-repeat;
      margin-left: -35px;
      top: 50%;
      margin-top: -10px;
    }
  }

  .mail {
    font-size: 13px;
    color: #ffffff;
    margin-left: 250px;
    position: relative;
    @media screen and (max-width: 1024px) {
      margin-left: 70px;
    }
    &:before {
      content: '';
      background-image: url(../images/mail.png);
      position: absolute;
      width: 15px;
      height: 10px;
      background-repeat: no-repeat;
      margin-left: -35px;
      top: 50%;
      margin-top: -5px;
    }
  }
}

.top-right {
  @media screen and (max-width: 1023px) {
    display: none;
  }

  .phone {
    color: #ffffff;
    font-size: 18px;
    margin-bottom: 10px;
    text-align: right;
    display: block;
    &:hover {
      color: #00d1f9;
    }
  }

  .header-btn-modal {
    color: #00d1f9;
    font-size: 13px;
    display: block;
    text-decoration: underline;
    font-family: "ProximaNova-Light";
    cursor: pointer;
    &:hover {
      text-decoration: none;
    }
  }
}



//nav
.menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  border-bottom: 1px solid #414141;
  border-top: 1px solid #414141;
  @media screen and (max-width: 1024px) {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #000000;
    height: 100vh;
    z-index: 101;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 120px 15px;
    transform: translateX(-100%);
    transition: .3s;
  }

  li {
    cursor: pointer;

    a {
      color: #ffffff;
      text-transform: uppercase;
      font-size: 13px;
      cursor: pointer;
      &:hover {
        color: #00d1f9;
      }
      @media screen and (max-width: 1024px) {
        font-size: 16px;
        margin-bottom: 15px;
        display: block;
      }
    }
  }

}

.logo-menu{
  display: none;
  @media screen and (max-width: 1024px) {
    display: block;
    margin-top: -90px;
    margin-bottom: 20px;
  }

}

.menu-open {
  transform: translateX(0%);
}

.nav-top {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100000;
  background-color: rgba(0, 0, 0, .9);
  border-bottom: 1px solid #414141;

  .menu {
    width: 100%;
    max-width: 1140px;
    margin: 0 auto;
    border-bottom: 1px solid transparent;
    border-top: 1px solid transparent;
  }
}

.menu-active {
  color: #00d1f9 !important;
}

.hamburger {
  flex-direction: column;
  min-width: 40px;
  min-height: 40px;
  border: 1px solid #fff;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  position: relative;
  z-index: 1000;
  display: none;
  @media screen and (max-width: 1024px) {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  span {
    width: 22px;
    height: 2px;
    background-color: #ffffff;
    display: block;
    margin-bottom: 3px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.hamburger-close {
  padding-top: 3px;

  .first {
    transform: rotate(-45deg);
    margin-bottom: 0;
  }

  .second {
    transform: rotate(45deg);
    margin-top: -2px;
  }

  .third {
    display: none !important;
  }
}

//promo
.promo {
  padding-top: 85px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  @media screen and (max-width: 1023px) {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding-top: 30px;
  }
}

.promo-title {
  color: #ffffff;
  font-size: 36px;
  display: block;
  margin-bottom: 50px;
  line-height: 1.2;

  @media screen and (max-width: 767px) {
    font-size: 19px;
    margin-bottom: 30px;
  }
  @media screen and (max-width: 374px) {
    font-size: 16px;
  }

}

.promo-info{
  @media screen and (max-width: 1023px) {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    text-align: center;
  }

}

//advantages

.advantages-title {
  font-size: 20px;
  color: #ffffff;
  display: block;
  margin-bottom: 60px;
  @media screen and (max-width: 1023px) {
    margin-bottom: 30px;
  }
  @media screen and (max-width: 767px) {
    font-size: 16px;
  }

}

.advantages-wrap {
  display: flex;
  @media screen and (max-width: 1023px) {
    margin-bottom: 40px;
  }

}

.advantages-block {
  margin-right: 120px;
  &:last-child {
    margin-right: 0;
  }
  @media screen and (max-width: 767px) {
    margin-right: 30px;
  }
}

.advantages-item {
  display: flex;
  align-items: center;
  margin-bottom: 60px;
  @media screen and (max-width: 1023px) {
    margin-bottom: 20px;
    text-align: left;
  }
  @media screen and (max-width: 575px) {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &:last-child {
    margin-bottom: 0;
  }

  &__img {
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__text {
    font-family: "ProximaNova-Light";
    color: #ffffff;
    font-size: 15px;
    display: block;
    line-height: 1.3;
    margin-left: 40px;
    br {
      @media screen and (max-width: 575px) {
        display: none;
      }
    }
    @media screen and (max-width: 575px) {
      margin-left: 0;
      margin-top: 15px;
      font-size: 13px;
    }
  }
}

//block-form

.block-form {
  background-color: #ffffff;
  width: 100%;
  max-width: 360px;
  padding: 37px 50px;
  @media screen and (max-width: 1023px) {
    max-width: 1023px;
  }
  @media screen and (max-width: 767px) {
    padding: 25px 15px;
  }
}

.block-form__title {
  display: block;
  text-align: center;
  font-size: 18px;
  color: #000000;
  margin-bottom: 30px;
  line-height: 1.2;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;

  input {
    width: 100%;
    height: 48px;
    border: 1px solid rgb(232, 232, 232);
    margin-bottom: 10px;
    font-size: 13px;
    text-align: center;
    font-family: "ProximaNova-Light";
    padding: 0 10px;
    color: #7c7c7c;
  }

  textarea {
    width: 100%;
    min-height: 85px;
    border: 1px solid rgb(232, 232, 232);
    font-size: 13px;
    text-align: center;
    font-family: "ProximaNova-Light";
    padding: 20px;
    color: #7c7c7c;
  }

  .submit {
    width: 200px;
    height: 55px;
    background-color: #23bac1;
    font-size: 13px;
    color: #ffffff;
    text-transform: uppercase;
    font-family: "ProximaNova-Bold";
    margin-top: 20px;
    box-shadow: 0px 6px 18px 0px rgba(28, 167, 173, 0.32);
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: .3s;
    z-index: 2;
    &:hover {
      background-color: #1f969c;
    }
  }
}

.block-form__footer {
  font-size: 11px;
  color: #7c7c7c;
  text-align: center;
  display: block;
  margin-top: 20px;
  font-family: "ProximaNova-Light";
  cursor: pointer;

  .link {
    text-decoration: underline;
    color: #7c7c7c;
    &:hover {
      text-decoration: none;
    }
  }
}

//product
.product {
  width: 100%;
  height: auto;
  position: relative;
  padding: 100px 0;
  @media screen and (max-width: 767px) {
    padding: 50px 0;
  }

  .container {
    position: relative;
  }
}

.lego1 {
  position: absolute;
  z-index: 100;
  right: -90px;
  top: -220px;
  @media screen and (max-width: 1365px) {
    display: none;
  }
}

.lego2 {
  position: absolute;
  z-index: 100;
  left: -190px;
  top: -220px;
  @media screen and (max-width: 1365px) {
    display: none;
  }
}

.lego3 {
  position: absolute;
  z-index: 100;
  right: -90px;
  top: -220px;
  @media screen and (max-width: 1365px) {
    display: none;
  }
}

.instruments {
  background-color: #f9f9f9;
}

.product-wrap {
  width: 100%;
  margin: 0 auto;
  margin-top: 100px;
  @media screen and (max-width: 767px) {
    margin-top: 50px;
  }
}

.product-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 0 auto;
  max-width: 270px;
}

.product-item__img {
  width: 269px;
  height: 270px;
  position: relative;
  overflow: hidden !important;
  top: 0;

  .owl-nav {
    display: none !important;
  }

  img {
    //position: absolute;
    //left: 50%;
    //top: 50%;
    //transform: translate(-50%, -50%);
  }
}

.product-item__name {
  display: block;
  font-family: "ProximaNova-Light";
  color: #000000;
  font-size: 16px;
  text-align: center;
  line-height: 1.2;
}

.product-item__price {
  display: block;
  font-size: 18px;
  color: #000000;
  font-family: "ProximaNova-Bold";
  margin: 20px 0 25px 0;
}

.product-item__btn {
  font-size: 13px;
  color: #23bac1;
  text-transform: uppercase;
  font-family: "ProximaNova-Bold";
  display: block;
  width: 198px;
  height: 53px;
  border: 1px solid rgb(35, 186, 193);
  cursor: pointer;
  background-color: transparent;
  position: relative;
  padding-left: 35px;
  transition: .3s;
  letter-spacing: .5px;
  &:hover {
    background-color: rgb(35, 186, 193);
    color: #ffffff;
  }
  &:hover {
    &:before {
      background-image: url(../images/buy1.png);
    }
  }
  &:before {
    content: '';
    background-image: url(../images/buy.png);
    background-repeat: no-repeat;
    width: 17px;
    height: 16px;
    position: absolute;
    margin-left: -37px;
    top: 50%;
    margin-top: -8px;
    transition: .3s;
  }
}

//owl-nav

.owl-nav {
  display: block !important;
}

.owl-prev {
  background-color: transparent !important;
  border-top: 3px solid #cccccc !important;
  border-right: 3px solid #cccccc !important;
  width: 22px;
  height: 22px;
  position: absolute;
  left: -50px;
  top: 50%;
  transform: rotate(-135deg);
  @media screen and (max-width: 1199px) {
    left: 0;
  }
  &:hover {
    border-top: 3px solid #23bac1 !important;
    border-right: 3px solid #23bac1 !important;
  }
}

.owl-next {
  background-color: transparent !important;
  border-top: 3px solid #cccccc !important;
  border-right: 3px solid #cccccc !important;
  width: 22px;
  height: 22px;
  position: absolute;
  right: -50px;
  top: 50%;
  transform: rotate(45deg);
  @media screen and (max-width: 1199px) {
    right: 0;
  }
  &:hover {
    border-top: 3px solid #23bac1 !important;
    border-right: 3px solid #23bac1 !important;
  }
}

//owl-dots
.owl-dots {
  display: flex;
  justify-content: center;
  margin-top: 40px;

  .owl-dot {
    margin-top: -50px;
    span {
      width: 8px;
      height: 8px;
      position: relative;
      margin: 0 auto !important;
      background-color: #e8e8e8 !important;
      margin-right: 4px !important;
      cursor: pointer;
      display: block !important;
      -webkit-border-radius: 100%;
      -moz-border-radius: 100%;
      border-radius: 100%;
    }
  }

  .active {
    span {
      display: inline-block !important;
      background-color: #23bac1 !important;
      margin-top: 3px !important;
    }
  }
}

//find
.find {
  width: 100%;
  height: auto;
  position: relative;
  background-color: #f9f9f9;
  padding: 100px 0;
  @media screen and (max-width: 767px) {
    padding: 50px 0;
  }
}

.block-form__find {
  background-color: transparent;
  width: 100%;
  max-width: 1200px;
  padding: 45px 0 0 0;

  .form {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 70px;
    @media screen and (max-width: 1023px) {
      flex-direction: column;
    }

    input {
      margin-bottom: 0;
      margin-right: 30px;
      width: 100%;
      @media screen and (max-width: 1023px) {
        margin-right: 0;
        margin-bottom: 10px;
      }
    }

    textarea {
      min-height: 48px;
      padding: 14px 10px 0 10px;
      margin-bottom: 0;
      margin-right: 30px;
      width: 100%;
      @media screen and (max-width: 1023px) {
        margin-right: 0;
        margin-bottom: 20px;
      }
    }
    .submit {
      margin-top: 0;
      width: 100%;
      height: 48px;
      @media screen and (max-width: 1023px) {
        max-width: 200px;
      }
    }
  }
}

//order
.order {
  width: 100%;
  height: auto;
  position: relative;
  background-image: url(../images/bg-order.png);
  -webkit-background-size: cover;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 100px 0;
  @media screen and (max-width: 767px) {
    padding: 50px 0;
  }

  .title {
    color: #ffffff;
  }
}

.order-wrap {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 100px;
  @media screen and (max-width: 767px) {
    margin-top: 50px;
  }
  @media screen and (max-width: 575px) {
    justify-content: space-between;
  }
}

.order-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  flex-basis: calc(100% / 4);
  @media screen and (max-width: 1023px) {
    flex-basis: calc(100% / 2);
    margin-bottom: 30px;
  }
  @media screen and (max-width: 575px) {
    flex-basis: calc(100%;
  }

  img {
    width: 100%;
    max-width: 98px;
    @media screen and (max-width: 575px) {
      max-width: 70px;
    }
  }

  .order-text {
    color: #ffffff;
    display: block;
    font-size: 16px;
    font-family: "ProximaNova-Light";
    margin-top: 30px;
    line-height: 1.3;
    br {
      @media screen and (max-width: 575px) {
        display: none;
      }
    }
    @media screen and (max-width: 575px) {
      margin-top: 15px;
    }
  }

  .order-modal {
    font-size: 15px;
    color: #23bac1;
    text-decoration: underline;
    margin-top: 20px;
    cursor: pointer;
    @media screen and (max-width: 575px) {
      font-size: 14px;
    }
    &:hover {
      text-decoration: none;
    }
  }
}

//delivery
.delivery {
  width: 100%;
  height: auto;
  position: relative;
  padding: 100px 0;
  @media screen and (max-width: 767px) {
    padding: 50px 0;
  }
}

.delivery-wrap {
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 100px;
  @media screen and (max-width: 767px) {
    margin-top: 50px;
  }
}

.delivery-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-basis: calc(100% / 4);
  @media screen and (max-width: 1023px) {
    flex-basis: calc(100% / 2);
    margin-bottom: 30px;
  }
  @media screen and (max-width: 575px) {
    flex-basis: calc(100%);
    margin-bottom: 30px;
  }

  &__img {
    height: 60px;
    width: 60px;
    display: flex;
    align-items: flex-end;
    justify-content: center;
  }

  &__text {
    display: block;
    text-align: center;
    margin-top: 30px;
    font-size: 16px;
    line-height: 1.3;
    font-family: "ProximaNova-Light";

    span {
      font-family: "ProximaNova-Bold";
    }
  }
}

//contacts
.contacts {
  padding-top: 60px;
  background-color: #f9f9f9;
  @media screen and (max-width: 767px) {
    padding: 50px 0;
  }
}

.contacts-wrap {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding-bottom: 65px;
  @media screen and (max-width: 575px) {
    flex-direction: column;
  }
}

.contacts-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  @media screen and (max-width: 575px) {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 20px;
  }

  a, span {
    font-size: 16px;
    color: #000000;
    margin-top: 30px;
    @media screen and (max-width: 575px) {
      margin-top: 0;
      margin-left: 20px;
    }
  }

  .mail {
    text-decoration: underline;
  }
}

.map {

  iframe {
    width: 100%;
    height: 420px;
  }
}

//faq
.faq {
  width: 100%;
  height: auto;
  padding: 65px 0;
  position: relative;
  @media screen and (max-width: 767px) {
    padding: 50px 0;
  }
}

.faq-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  @media screen and (max-width: 575px) {
    flex-direction: column;
  }
}

.faq-text {
  font-size: 22px;
  color: #000000;
  margin-right: 15px;
  display: block;
  cursor: pointer;
  @media screen and (max-width: 575px) {
    margin-bottom: 10px;
  }
}

.faq-btn {
  color: #00d0f9;
  font-size: 22px;
  border-bottom: 1px solid #00d0f9;
  cursor: pointer;
  display: block;
  &:hover {
    border-bottom: 1px solid transparent;
  }
}

//footer
.footer {
  width: 100%;
  height: auto;
  position: relative;
  background-color: #191919;

  .top {
    @media screen and (max-width: 575px) {
      flex-direction: column;
    }
  }

  .top-contacts {
    @media screen and (max-width: 1023px) {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin: 20px 0;
    }
    @media screen and (max-width: 575px) {
      align-items:center;
    }

    .mail {
      @media screen and (max-width: 1023px) {
        margin-left: 0;
        margin-top: 15px;
        text-align: center;
      }
      @media screen and (max-width: 575px) {
        text-align: center;
        display: block;
      }
      &:before {
        @media screen and (max-width: 575px) {
          display: none;
        }
      }

    }

    .city{
      @media screen and (max-width: 575px) {
        text-align: center;
      }

      &:before {
        @media screen and (max-width: 575px) {
          display: none;
        }
      }
    }
  }

  .top-right {
    display: block;

    .phone {
      @media screen and (max-width: 767px) {
        text-align: center;
      }
    }
  }
}

//modal
.modal {
  width: 100%;
  max-width: 360px;
  height: auto;
  background-color: transparent;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 10002;
  display: none;

  .block-form {
    padding-top: 40px;
  }
}

.modal-close {
  position: absolute;
  right: 10px;
  top: 10px;
  opacity: 1;
  z-index: 101;
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  &__first {
    position: absolute;
    background-color: #000;
    width: 20px;
    height: 2px;
    top: 11px;
    left: 3px;
    transform: rotate(45deg);
  }
  &__two {
    position: absolute;
    background-color: #000;
    width: 20px;
    height: 2px;
    top: 11px;
    left: 3px;
    transform: rotate(135deg);
  }
}

.overlay {
  background-color: rgba(0, 0, 0, 0.9);
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 10001;
  display: none;
  cursor: pointer;
}

//thankyou
.thankyou {
  width: 100%;
  max-width: 520px;
  height: auto;
  background-color: #fff;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 10002;
  padding: 75px 10px 85px 10px;
  display: none;
  overflow: hidden;
}

.modal-close {
  position: absolute;
  right: 10px;
  top: 10px;
  cursor: pointer;
  opacity: 1;
  z-index: 101;
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  &__first {
    position: absolute;
    background-color: #000;
    width: 20px;
    height: 2px;
    top: 11px;
    left: 3px;
    transform: rotate(45deg);
  }
  &__two {
    position: absolute;
    background-color: #000;
    width: 20px;
    height: 2px;
    top: 11px;
    left: 3px;
    transform: rotate(135deg);
  }
}

.thankyou__title {
  display: block;
  text-align: center;
  font-size: 36px;
  color: #222;
  font-weight: 900;
  text-transform: uppercase;
}

.thankyou__subtitle {
  font-size: 24px;
  color: 222;
  display: block;
  text-align: center;
  margin-top: 20px;
}

